"use client";

import { useState } from "react";
import { Loader2 } from "lucide-react";

interface AIBlendButtonProps {
  canvasImage: string | null;
  backgroundUrl: string;
  disabled?: boolean;
  onBlendComplete: (blendedImage: string) => void;
}

export default function AIBlendButton({
  canvasImage,
  backgroundUrl,
  disabled = false,
  onBlendComplete,
}: AIBlendButtonProps) {
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const handleClick = async () => {
    console.log("🎯 AI融合按钮被点击");
    console.log("📦 当前状态:", {
      hasCanvasImage: !!canvasImage,
      backgroundUrl,
      disabled,
      isLoading
    });

    if (!canvasImage) {
      console.error("❌ 错误：canvasImage 为空");
      setError("请先调整人物位置");
      return;
    }

    setIsLoading(true);
    setError(null);

    try {
      console.log("🔄 开始转换背景图为 base64...");
      // 将背景图转换为 base64
      const backgroundResponse = await fetch(backgroundUrl);
      const backgroundBlob = await backgroundResponse.blob();
      const backgroundBase64 = await new Promise<string>((resolve) => {
        const reader = new FileReader();
        reader.onloadend = () => resolve(reader.result as string);
        reader.readAsDataURL(backgroundBlob);
      });

      console.log("✅ 背景图转换完成");

      console.log("🔄 开始调用 AI 融合 API...");
      const response = await fetch("/api/ai-blend", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          foreground: canvasImage,
          background: backgroundBase64,
        }),
      });

      console.log("📡 API 响应状态:", response.status);
      const data = await response.json();
      console.log("📄 API 响应数据:", data);

      if (!response.ok) {
        throw new Error(data.error || `HTTP error! status: ${response.status}`);
      }

      if (!data.result) {
        throw new Error("AI 融合失败：未返回结果");
      }

      console.log("✅ AI 融合成功，调用 onBlendComplete");
      onBlendComplete(data.result);
    } catch (error) {
      console.error("❌ AI 融合错误:", error);
      setError(error instanceof Error ? error.message : "AI 融合失败，请重试");
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="space-y-4">
      <button
        onClick={handleClick}
        disabled={disabled || isLoading || !canvasImage}
        className={`
          w-full py-2 px-4 rounded-md font-medium
          ${disabled || isLoading || !canvasImage
            ? "bg-gray-300 text-gray-500 cursor-not-allowed"
            : "bg-blue-600 text-white hover:bg-blue-700"
          }
          transition-colors duration-200
          flex items-center justify-center
        `}
      >
        {isLoading ? (
          <>
            <Loader2 className="mr-2 h-4 w-4 animate-spin" />
            正在融合中...
          </>
        ) : (
          "开始 AI 融合"
        )}
      </button>
      
      {error && (
        <div className="text-sm text-red-500 bg-red-50 p-3 rounded-md">
          {error}
        </div>
      )}
    </div>
  );
} 